/*Vue動畫*/
.detail-enter-active,.detail-leave-active{ /*動畫時間、曲線函數*/
	-webkit-transition: 0.5s all;
	transition: 0.5s all;
}
.detail-enter/*動畫開始狀態*/,.detail-leave-to/*動畫離開結束狀態*/{
	opacity: 0;
}
.detail-enter-to/*動畫開始結束狀態*/,.detail-leave/*動畫離開*/{
	opacity: 1;
}

