/*
	flex: flex-grow | flex-shrink | flex-basis
	flex-grow必填，其他選填。
	默認值 : flex 0 1 auto;
	flex-grow: 定義放大比例,默認0,如果存在剩餘空間，也不放大;
	flex-shrink: 定義縮小比例，默認1，如果空間不足，該項目會被縮小，flex-shrink屬性為0時，其他項目為1時，空間不足時，前者不縮小;
	flex-basis: 定義了分配多餘空間之前，項目佔據主軸空間，瀏覽器根據這個屬性，計算佔據是否有多餘空間，默認值auto，即項目的本來大小，設為跟width或height屬性值一樣，則項目佔據固定空間;	
*/
